.miniature h1,
.miniature h2,
.miniature h3,
.miniature h4 {
  margin-top: 0px;
}
.miniature {
  position: relative;
  display: flex;
  gap: 0.8rem;
  border-radius: 6px;
  overflow: hidden;
  padding: 0 1rem 0 0;
  width: 368px;
  margin: 0;
  background-color: #52315214;
}
.padding {
  padding: 0.8rem;
}
.textBox {
  margin: auto;
  max-width: 160px;
  text-align: center;
  padding: 0.8rem;
}
.imgFrame {
  display: flex;
  object-fit: cover;
  aspect-ratio: 1/1;
  background-color: #aa9eae41;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12),
    inset 0px -2px 4px -1px #ad8fb93e, inset 0px -4px 5px 0px #ad8fb926,
    inset 0px -1px 10px 0px #ad8fb918;

  border-radius: 6px;
  background: radial-gradient(
      ellipse closest-side at 25% 76%,
      #3500463a 8%,
      #c300ff00 130%
    ),
    radial-gradient(
      ellipse closest-side at 79% 22%,
      #3500463a 8%,
      #c300ff00 90%
    ),
    radial-gradient(
      ellipse closest-side at 67% 40%,
      #3500463a 2%,
      #c300ff00 35%
    ),
    radial-gradient(
      ellipse closest-side at 80% 30%,
      #f7dfff3e 15%,
      #c300ff00 80%
    ),
    radial-gradient(
      ellipse closest-side at 58% 49%,
      #f7dfff1e 8%,
      #c300ff00 30%
    ),
    radial-gradient(
      ellipse farthest-corner at 30% 86%,
      #db6dff3e 2%,
      #ffffff00 30%
    ),
    radial-gradient(
      ellipse farthest-corner at 80% 20%,
      #db6dff3e 2%,
      #ffffff00 30%
    ),
    radial-gradient(
      ellipse farthest-corner at 20% 10%,
      #9a27c023 2%,
      #ffffff00 20%
    ),
    conic-gradient(#1a1a1a27, #8400ff3c, #1a1a1a27, #8400ff3c, #1a1a1a27);
}
.figcaption {
  flex-shrink: 1;
  margin: auto 0;
}
.img {
  aspect-ratio: 1/1;
  position: relative;
  z-index: 0;
  display: inline-block;
  white-space: pre;
  min-width: 100px;
  max-width: 160px;
  object-fit: inherit;
  min-height: 100px;
  text-align: center;
  justify-content: center;
  line-height: 1.2em;
  font-size: 1.4em;
  border-radius: 6px;
  overflow: hidden;
}
.themeDark::before {
  content: '';
  aspect-ratio: 1/1;
  border-radius: 6px;
  top: 0px;
  left: 0px;
  height: 100%;
  background-color: #0000005d;
  z-index: 100;
  position: absolute;
}
.tmdb {
  background: #0d253f;
  object-fit: contain;
}
